
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'language-swich',
  data() {
    return {
      langDefault: this.$langDefault('admin'),
      langsAdmin: Object.keys(this.$langs('admin')),
    };
  },

  methods: {
    // Set lang default
    setLangDefault() {
      this.$setCokieLangDefault({ langDefault: this.langDefault });
      location.reload();
    },
  },
});
